.app--card-user{
  width: 250px;
  min-height: 200px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  box-shadow: 2px 6px 20px #aaaaaa;
}

.app--card-image{
  border-radius: 50%;
  max-width: 70px;
  height: 70px;
}

.app--card-name{
  text-transform: uppercase;
  margin-top: 5px;
  font-size: 18px;
  padding: 10px;
}

.app--card-branch{
  margin-top: 5px;
  font-size: 18px;
  text-transform: capitalize;
  font-weight: 100;
}

.reason__user {
  display: flex;
  flex-direction: row;
  text-align: left;
  width: 40%;
  height: 10px;
  min-height: 100px;
  box-shadow: none;
  .app--card-image{
    max-width: 30px;
    height: 30px;
  }
  .app--card-name{
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 200;
    color: black;
  }
  .app--card-branch{
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    color:gray;

  }
}

@media all and (min-width: 1200px) {
.reason__user{
  margin-left: 50px;
}
.app--card-user{
  border-radius: 20px;
  padding: 9px;
}

.app--card-image{
  margin-left:10px;
  max-width: 70px;
  height: 70px;
}
.app--card-name{
  font-size: 16px;
}
.app--card-branch{
  margin-top: 10px;
  font-size: 14px;
}
}
